.title-container {
  margin-top: 30px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100%;
}

.title {
  font-size: 36px;
  font-family: "shadows into light", cursive;
  font-weight: bold;
  color: #333;
  text-align: center;
  max-width: 600px;
  letter-spacing: 0.1em;
  margin-bottom: 0.5rem;
}

.title2 {
  margin-top: 0.5rem;
  font-size: 32px;
  font-family: "Rubik", sans-serif;
  font-weight: bold;
  color: #333;
  text-align: center;
  max-width: 600px;
  white-space: nowrap;
  margin: 0 auto;
  letter-spacing: 0.1em;
  overflow: hidden;
  animation: typing 3.5s steps(40, end), blink-caret 0.75s step-end infinite;
}
/* The typing effect */
@keyframes typing {
  from {
    width: 0;
  }
  to {
    width: 100%;
  }
}

.body {
  font-family: "Rubik", sans-serif;
  font-size: 16px;
  color: #333;
  margin-bottom: 30px;
  max-width: 600px;
  text-align: center;
  padding: 10px;
}
